import { Notify } from 'vant';

class ValidForm {

	// 验证表单
	vaild(options) {
		for(let key in options) {
			// 验证失败
			if(!options[key].reg.test(options[key].value)) {
				
				// 消息提示
				Notify({
					type: 'warning',
					message: options[key].errorMsg
				});
				return false;
			}
		}

		// 验证通过
		return true;
	}

	say() {
		console.log(1231);
	}
}

export const validForm = new ValidForm();

